<template>
    <div @click="runVideo">
        <img src="@/static/wave22.png" style="position:absolute;height:100vh;width:100vw;" alt="">
        <div class="p-10">
            <div class="row">
                <div class="col-6 text-center">
                    <emitting-analog-clock id='main-clock' 
                    :size='530' 
                    :showDigital="false"
                    :shape="'circle'"
                    :backgroundColor="'#09303aff'"
                    :date="false"
                    :color="'#f1ff58'"
                    :smooth="true"
                    ></emitting-analog-clock>
                </div>
                <div class="col-6 row justify-content-end">
                    <div class="col-4">
                        <img src="@/static/hasnur2.png" style="height:180px;width:200px;" alt="">
                    </div>
                    <div class="col-4">
                        <img src="@/static/hdi.png" style="height:200px;width:200px;" alt="">
                    </div>
                    <div class="col-12">
                        <div style="height:120px;"></div>
                         <video id="myVideo" autoplay loop controls class="rounded-lg shadow">
                            <source src="@/static/lobby2.mp4" type="video/mp4">
                            Your browser does not support the video tag.
                        </video>
                    </div>
                </div>
                <div class="col-6">
                    <div id="clock" style="position:absolute;">
                        <p class="date">{{ date }}</p>
                        <p class="time">{{ time }}</p>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import EmittingAnalogClock from 'vue-emitting-analog-clock';
export default {
    data(){
        return{
        time: '',
        date: ''
        }
    },
    methods: {
        runVideo(){
            var video = document.getElementById("myVideo");
            video.play()
        },
        runClock(){
            let that=this;
            var week = ['SUN', 'MON', 'TUE', 'WED', 'THU', 'FRI', 'SAT'];
            var timerID = setInterval(updateTime, 1000);
            updateTime();
            function updateTime() {
                var cd = new Date();
                that.time = zeroPadding(cd.getHours(), 2) + ':' + zeroPadding(cd.getMinutes(), 2) + ':' + zeroPadding(cd.getSeconds(), 2);
                that.date = zeroPadding(cd.getFullYear(), 4) + '-' + zeroPadding(cd.getMonth()+1, 2) + '-' + zeroPadding(cd.getDate(), 2) + ' ' + week[cd.getDay()];
            };

            function zeroPadding(num, digit) {
                var zero = '';
                for(var i = 0; i < digit; i++) {
                    zero += '0';
                }
                return (zero + num).slice(-digit);
            }
        },
    },
    mounted() {
        this.runClock();
    },
  components: {EmittingAnalogClock}
};
</script>
<style>
.test{
    color:#f1ff58;
}

html,body {
    height: 100%;
}
body {
    background: #0f3854;
    background: radial-gradient(ellipse at center,  #0a2e38  0%, #000000 70%);
    background-size: 100%;
}
p {
    margin: 0;
    padding: 0;
}
#clock {
    font-family: 'Share Tech Mono', monospace;
    color: #ffffff;
    text-align: center;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    color: #daf6ff;
    text-shadow: 0 0 20px rgba(10, 175, 230, 1),  0 0 20px rgba(10, 175, 230, 0);
    .time {
        letter-spacing: 0.05em;
        font-size: 80px;
        padding: 5px 0;
    }
    .date {
        letter-spacing: 0.1em;
        font-size: 24px;
    }
    .text {
        letter-spacing: 0.1em;
        font-size: 12px;
        padding: 20px 0 0;
    }
}
</style>